// Font 'PT Sans'
@import url('https://fonts.googleapis.com/css2?family=PT+Sans&display=swap');

@import './variables.scss';
@import './mixins.scss';

* {
    padding : 0;
    margin : 0;
    box-sizing: border-box;
    font-family: 'PT Sans',sans-serif;
}


body {
    color : white;
    font-family : "PT Sans",sans-serif;
    perspective : 2px;
    overflow-x : hidden;
    position: relative;

}


button {
    outline : none;
    border : none;
    cursor : pointer;
}


section {
    height: 100vh;
}

a {
    text-decoration : none;
}


nav {

    display: flex;
    align-items: center;
    width: 100%;
    padding : 1rem 1.4rem;
    transition : height .5s ease;
    background-color : #292929;
    @include respond('mobile') {
        padding : 1.4rem;
    }
    h1 {
        letter-spacing : 3px;
        @include font-scale($font-size : 2rem);
        transition : all .5s ease;
    }

    .nav-list {
        width: 100%;
        position: relative;
        margin-left : 2rem;
        transition : all .5s ease;
        &-link {
            color : rgb(243, 243, 243);
            display: inline-block;
        }

        &-auth {
            display: inline-block;
            position: absolute;
            margin-top : -.5rem;
            right : 0;

            button  {
                font-family: 'PT Sans', sans-serif;
                font-weight: 600;
                font-size : 1.2rem;
                padding : .75rem 1.5rem;
                outline : none;
                border-radius: 3px;
                color : white;
            }

            .btn-signup {
                background-color : #F06292;
            }

            .btn-login  {
                background-color: transparent;
            }
        }

        // Media query 
        @include respond('mobile') {
            display: none;
        }
    }
    
    li {
        list-style : none;
        transition : all .5s ease;
        font-family: 'PT Sans',sans-serif;
        @include font-scale($font-size : 1.3rem);
        display : inline-block;
        margin : .5rem;
    }

    .hamburger-wrapper {
        visibility: hidden;
        
        button {
            width: 60px;
            height : 60px;
            padding : .5rem;
            background-color: #242424;
            border-radius : 4px;
            

            div {
                width: 100%;
                height : 4px;
                background-color : white;
                border-radius  : 2px;
                margin : .5rem 0rem;
                transition : transform .4s ease;
            }
        }
        @include respond('mobile') {
            visibility: visible;
            position : fixed;
            top : .6rem;
            right : .6rem;
        }
    }


}


@include respond('mobile') {
    .nav-active {
        height: 100vh;
        position: fixed;
        justify-content: center;
        flex-direction: column;
        background-color: rgb(61, 61, 61);
        z-index: 2;
    
        h1 {
            text-align: center;
            font-size : 2.5rem;
        }
    
        .nav-list {
            display: block;
            justify-content: center; 
            margin : 0;       
    
            &-link,
            &-auth {
                text-align: center;
                margin : 2rem 0rem;
                display: block;
                position: relative;
 
                li  {
                    margin : 2rem 0rem;
                    display: block;
                    font-size : 1rem;
                }
            }

            &-link {
                li {
                    font-size : 2rem;
                }
            }
    
            &-auth  {
                li {
                    display: inline-block;
                    margin : .5rem;
                    
                    button {
                        font-size : 1.7rem;
                    }
                }
            }
        }
    
        .hamburger-wrapper {
            button {
    
                div {
                    margin : 0 auto;
    
                }
                div:nth-child(1) {
                    transform : translateY(4.5px) rotate(45deg);
                }
    
                div:nth-child(2) {
                    opacity : 0;
                }
    
                div:nth-child(3) {
                    transform : translateY(-4.5px) rotate(-45deg);
                }
            }
        }
    }
}

#jumbotron {
    display: flex;
    height: 90vh;
    background-color : rgb(41, 41, 41);
    @include respond('mobile') {
        display: block;
    }

    & .caption {
        padding : 5rem 3.5rem;
        flex : 50%;
        overflow : hidden;

        * {
            transition: transform 1s ease-in;
        }
    }

    & .img {
        width: 100%;
        height: 100%;
        flex : 50%;
        background-color: rgb(41, 41, 41);
        position: relative;
        overflow: hidden;
        @include respond('mobile') {
            visibility: hidden;
        }

        img {
            position: relative;
            margin-left : 2rem;
            z-index : 2;
            padding: 4rem 4rem;
            object-fit: contain;
            width: 100%;
            height: 100%;
            right : 0;
            transition: transform 1s ease-out;
        }



        .top-oval,
        .bottom-oval {
            position : absolute;
            width : calc((100vw / 2) * 0.65);
            height : calc((100vw / 2) * 0.65);
            border-radius: 50%;
            background-color : #F06292;
        }

        .top-oval {
            right : calc((100vw / 2) * -0.17);
            top : calc((100vw / 2) * -0.17);
        }

        .bottom-oval {
            right : calc((100vw / 2) * -0.17);
            bottom : calc((100vw / 2) * -0.17);
        }
    }


    h1 {
        margin-bottom: 2rem;
        @include font-scale(3rem);
    }

    h3 {
        margin-bottom: .7rem;
        @include font-scale(1.7rem)
    }
    
    p {
        margin-bottom: 2rem;
        @include font-scale(1.2rem);
    }

    button {
        padding : .6rem 1.5rem;
        font-weight: 600;
        @include font-scale(1.4rem);
        background-color : #F06292;
        color : white;
        transition: transform .5s ease;
    }

    button:hover {
        transform: scale(1.1);
    }
}



#waifu {   
    position: relative;
    display: flex;
    flex-direction: column;
    padding : 1.5rem 2.5rem ;
    background-color: #fefefefe;
    color : #303030;
    height: auto;
    
    h1 {
        text-align: center;
        @include font-scale(2.5rem);
        margin : 1.3rem 0rem ;
        
    }



    .title {
        text-align : center;
        position: relative;
        padding-bottom : 1rem;
        @include font-scale(2.5rem);
    }

    .title::after{
        content: "";
        position: absolute;
        width: 50%;
        bottom: 0;
        left: 25%;
        border-bottom: 3px solid #303030;
    }

    .card-wrapper {
        position: relative;
        margin : 2rem 0rem;
        width: 100%;
        display : flex;
        flex-wrap : wrap;
        justify-content: center;

        @include respond('mobile') {
            margin : 2rem auto;
            display : block;
        }

        .waifu-card {
            max-width: 400px;
            position: relative;
            flex : 1 0 350px;
            // align-items: stretch;
            flex-direction: column;
            justify-content: space-between;
            padding-bottom: 4rem;
            background-color : #303030;
            color : #fefefefe;
            line-height: 1.8;
            box-shadow : 0px 3px 10px rgba(34, 34, 34, 0.733);
            border-radius: 2px;
            margin : 2rem;

            @include respond('mobile') {
                margin : 2rem 0rem;
            }

            &-title {
                display: block;
                text-align : center;
                width: 100%;
                display: block;
                padding-top : .5rem;
               @include font-scale(2rem);
            }

            &-price {
                font-size : 1.3rem;
                text-align : center;
                letter-spacing: 5px;
                margin : 1rem 0rem 2rem;
            }

            img {
                display: block;
                margin: auto;
                max-height: 20rem;
                object-fit: contain;
                object-position: center;
            }



            &-content {
                flex : 1 0 60%;
                display: flex;
                position: relative;
                flex-direction: column;
                background-color: inherit;
                padding : 1rem 1rem 1rem 1.5rem ;
                box-sizing : border-box;
                text-decoration : none;

                .tags {
                    display: flex;
                    flex-wrap : wrap;
                    text-align: center;
                    align-items: flex-end;
                    
                    p {
                        cursor : pointer;
                        text-transform: uppercase;
                        flex : 1 0 25%;
                        font-weight: 600;
                        @include font-scale(1.1rem);
                        margin : .5rem;
                        padding : .5rem .7rem;
                        background-color : #F06292;
                        border-radius: 5px;
                    }

                    p:nth-child(2) {
                        background-color: #629ff0;
                    }

                    p:nth-child(3) {
                        background-color: #f0d662;
                    }

                    p:nth-child(4) {
                        background-color: #f06262;
                    }

                    p:nth-child(5) {
                        background-color: #72f062;
                    }
                }

                .waifu-desc {
                    padding : 1rem 0rem;
                    text-indent: 50px;
                    font-size: 1.1rem;
                    line-height: 1.8;
                }
            }



                                                            
            .btn-detail {
                position : absolute;
                left : 1rem;
                bottom : 1rem;
                color : white;
                font-weight: 600px;
                padding : .5rem 1rem;
                background-color  : #F06292;
                text-align : center;
                border-radius : 4px;
                transition: padding 200ms ease;
            }

            .btn-detail:hover {
                padding : .7rem 1.4rem;
            }

        }
    }
}


#choose-type {
    background-color : #e7e7e7;
    position: relative;
    height : 60vh;
    margin-bottom: 3rem;

    .type-wrapper {
        background-color: #eeeeee;
        height: 100%;
        color : #303030;
        display : flex;
        @include respond('mobile') {
            flex-direction: column;
        }

        div {
            position: relative;
            cursor: pointer;
            flex : 50%;
            width : 100%;
            height : 100%;
            display : flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            @include font-scale(2.5rem);
            
            h1 {
                z-index : 2;
            }
        }
        
        .long-hair {
            background-color : #303030;
            color: white;
        }

        .short-hair {
            background-color: white;
            color : #303030;
        }
        .long-hair:hover {
            background-image: url('../../img/long-hair.jpg');
            background-size: cover;
            background-position : center;
            background-repeat: no-repeat;
            color : white;

        }

        .short-hair:hover {
            background-image: url('../../img/short-hair.jpg');
            background-size: cover;
            background-position : center;
            color : #303030;
            background-repeat: no-repeat;
        }

        .long-hair:hover::after {
            content : "";
            position: absolute;
            width : 100%;
            height : 100%;
            background-image : linear-gradient(to left,#3f3f3f,transparent);

        }

        .long-hair:hover::after {
            content : "";
            position: absolute;
            width : 100%;
            height : 100%;
        }

        .short-hair:hover::after {
            content : "";
            position: absolute;
            width : 100%;
            height : 100%;
            background-image : linear-gradient(to right, #d3d3d3e0,transparent);

        }



    }
}

#testimoni {
    color  : #303030;
    align-items: center;
    padding : 2rem;
    padding-bottom : 4rem;
    height: 100%;
    @include respond('mobile') {
        padding : 0;
    }

    .testimoni-title {
        display: block;
        text-align : center;
        padding : 2rem 0rem;
        @include font-scale(2.5rem);
    }

    .testimoni-wrapper {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        height: 100%;
        width: 100%;
        @include respond('mobile') {
            display: block;
        }

        .card {
            flex : 0 0 300px;
            margin : 0rem 1rem;
            display: flex;
            flex-direction: column;
            justify-content: center;
            text-align: center;
            @include respond('mobile') {
                margin : 2rem 0rem;
            }

            * {
                margin : 1rem 0rem;
            }

            .card-desc {
                @include respond('mobile') {
                    padding : 0rem 2rem;
                }
            }

            img {
                border-radius : 50%;
                height: 100px;
                width : 100px;
                margin : 0rem auto;
                background-color: #242424;
                border : none;
            }
        }
    }
}


footer {
    color : white;
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding : 2rem;
    background-color : #303030;

    div {
        text-align: center;
        flex : 33%;
    }

    ul {
        li {
            list-style : none;
            @include font-scale(1.2rem);
            margin : .25rem;
        }
    }

    .main-title {
            @include font-scale(2rem);
            margin-bottom: .5rem;
    }

    .list-item {
        @include font-scale(1.4rem);
        cursor : pointer;
    }

    .brand {
        text-align: left;

        &-sub-title {
            @include font-scale(1.4rem);
            margin : .25rem;
        } 
    }

    .contact {
        .list-item  {
            margin : 1rem;
            text-align: right;

            i {
                @include font-scale(3rem);
            }
        }
    }
}